.boton-menu {
    border-radius: 4px;
    background-color: #66CDAA;
    border: none;
    color: #FFF;
    text-align: center;
    font-size: 15px;
    padding: 10px;
    width: 150px;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
    cursor: pointer;
    margin: 5px;
}
.boton_chico {
    border-radius: 4px;
    background-color: #66CDAA;
    border: none;
    color: #FFF;
    text-align: center;
    font-size: 12px;
    padding: 7px;
    width: 125px;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
    cursor: pointer;
    margin: 5px;
}
.boton_chico:disabled {
    background-color: #6CAC;
    color: #CCC;
}
.boton_enano {
    border-radius: 4px;
    background-color: #66CDAA;
    border: none;
    color: #FFF;
    text-align: center;
    font-size: 12px;
    padding: 7px;
    width: 30px;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
    cursor: pointer;
    margin: 5px;
}
.button span {
    cursor: pointer;
    display: inline-block;
    position: relative;
    -webkit-transition: 0.5s;
    transition: 0.5s;
}
.button span:after {
    content: '\BB';
    position: absolute;
    opacity: 0;
    top: 0;
    right: -20px;
    -webkit-transition: 0.5s;
    transition: 0.5s;
}
.button:hover span {
    padding-right: 25px;
}
.button:hover span:after {
    opacity: 1;
    right: 0;
}
table {
      border-collapse: collapse;
      margin:auto;
}
th, td {
    border-bottom: 1px solid #ddd;
      padding: 8px;
}
tr:nth-child(even){background-color: #f2f2f2
}
table.greenTable {
    border: 2px solid #FFFFFF;
    background-color: #FFFFFF;
    text-align: center;
    border-collapse: collapse;
}
table.greenTable td, table.greenTable th {
    border: 1px solid #FFFFFF;
    padding: 3px 4px;
}
table.greenTable tbody td {
    font-size: 13px;
}
table.greenTable tr:nth-child(even) {
    background: #BDF0E3;
}
table.greenTable thead {
    background: #FFFFFF;
    border-bottom: 4px solid #333333;
}
table.greenTable thead th {
    font-size: 15px;
    font-weight: bold;
    color: #333333;
    text-align: center;
    border-left: 2px solid #333333;
}
table.greenTable thead th:first-child {
    border-left: none;
}
table.greenTable tfoot {
    font-size: 14px;
    font-weight: bold;
    color: #333333;
    border-top: 4px solid #333333;
}
table.greenTable tfoot td {
    font-size: 14px;
}
